.border{
    fill:none;
    stroke:green;
    stroke-width:1;
  }
  .sector{
    fill:none;
    stroke:#ADFFCC;
    stroke-width:50;
    
  }
  .wrapper {
      display: flex;
      align-items: center;
    .circWrap{
      box-shadow: 4px 4px 20px rgba(0,0,0,.5); 
      border-radius: 100%;
    }

    g{
        transform-origin: 50%;
        text {
            transform-origin: 50%;
            // transform: rotate(30deg);
            user-select: none;
          }
    }
    .g2 {
        // transform: rotate(45deg);
          circle {
            stroke:#F5E5B7;
          }
      }
    .g3 {
        // transform: rotate(90deg);
          circle {
            stroke:#E60161;
          }
    }
    .g4 {
        // transform: rotate(135deg);
          circle {
            stroke:#112B17;
          }
    }
    .g5 {
        // transform: rotate(180deg);
          circle {
            stroke:#A6292F;
          }
    }
    .g6 {
        // transform: rotate(225deg);
          circle {
            stroke:#0B6070;
          }
    }
    .g7 {
        // transform: rotate(270deg);
          circle {
            stroke:#603B0E;
          }
    }
    .g8 {
        // transform: rotate(315deg);
          circle {
            stroke:#3D5A76;
          }
    }
  }

  .arrow-7 {
    margin: 40px auto 10px auto;
    width: 38px;
    height: 100px;
    transform: rotate(90deg) translate(-25%,20%);
}
.arrow-7 span {
    display: block;
    width: 30px;
    height: 30px;
    border-bottom: 5px solid #337AB7;
    border-right: 5px solid #337AB7;
    transform: rotate(45deg) ;
    margin: -16px 0;
    animation: arrow-7 1.5s  infinite;
}
.arrow-7 span:nth-child(2){
    animation-delay: -0.2s;
}
.arrow-7 span:nth-child(3){
    animation-delay: -0.4s;
}
.arrow-7 span:nth-child(4){
    animation-delay: -0.6s;
}
.arrow-7 span:nth-child(5){
    animation-delay: -0.8s;
}
@keyframes arrow-7 {
    0%{
        opacity: 0;
        transform: translate(0,-20px) rotate(45deg);
    }
    80%{
        opacity: 1;
    }
    100%{
        opacity: 0;
        transform: translate(0,20px) rotate(45deg);
    }
}
  



@keyframes wheel {
    to {
        transform: rotate(-25deg);
    }
    from {
        transform: rotate(335deg);
    }
}